.burger{
    width:100%;
    margin:auto;
    height:250px;
    overflow:scroll;
    text-align:center;
    font-weight:bold;
    font-size:1.2rem;
}
@media (min-width: 1000px) {
    .burger{
        width:700px;
        height:600px;
    }
}
@media (min-width: 500px) {
    .burger{
        width:450px;
        height:400px;
    }
}
@media (min-width: 500px) {
    .burger{
        width:350px;
        height:300px;
    }
}
